import React, { useEffect, useRef, useContext } from "react";
import styled, { keyframes } from 'styled-components'
import { useLocomotiveScroll } from 'react-locomotive-scroll';
import { PopupContext } from '../PopupStore/PopupStore';

// import * as S from './styles/SvgText.styles.js';
// import {AnimatedPath} from "./styles/SvgText.styles.js";

const SvgTextWrap = styled.div`
  //padding-bottom:  100px;
  position: relative;
`

const emptyDash = keyframes`
  to {
    stroke-dasharray: 300;
    //stroke-dashoffset: -30;
  }
`

const fillDash = keyframes`
  to {
    stroke-dasharray: 520;
    stroke: #000000;
  }
`

const fillBG = keyframes`
  to {
    fill: #000;
  }
`

const fillOffset = keyframes`
  to {
    stroke-dashoffset: 0;
  }
`

const fillClipText = keyframes`
  to {
    background-position: 0 100%;
  }
`

const textShadow = keyframes`
  to {
    text-shadow: 1px 0px 3px rgba(0, 0, 0, 1);
  }
`

const opacityIn = keyframes`
  to {
    opacity: 1
  }
`

const AnimatedPath = styled.path`
  stroke-dasharray: 250;
  //stroke-dashoffset: 120;
  fill: transparent;
  stroke: #000000;
  transition: all 300ms ease-in-out;
  animation: ${fillDash} 2s ease-in 1400ms forwards , ${fillOffset} 2s ease-in 1400ms forwards;
`

const AnimatedPathFill = styled(AnimatedPath)`
  transition: all 300ms ease-in-out;
  animation: ${fillDash} 2s ease-in 1400ms forwards , ${fillOffset} 2s ease-in 1400ms forwards, ${fillBG} 1.5s ease-in 2000ms forwards
`;

const SvgPathWrap = styled.svg`
  cursor: pointer;
  
  &:hover {
    path {
      transition: all 300ms ease-in-out;
      stroke-dasharray: 300; !important;
    }
  }
`

const GrpSvg = styled.g`
  fill: #f0f2f6;
  pointer-events: bounding-box;
  &:hover {
    background: #000;
    path {
      transition: all 300ms ease-in-out;
      stroke-dasharray: 300;
//      stroke: yellow
    }
  }
`

const Clipped = styled.div`
  position: absolute;
  width: max-content;
  font-size: 130px;
  font-weight: 800;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-size: 100% 200%;
  background-image: linear-gradient(to top, #000000 0% 50%, transparent 50% 100%);
  background-position: 0 0;
  text-shadow: none;
  transition: background-position 300ms, text-shadow 300ms;
  animation: ${fillClipText} 2000ms ease-in 1400ms forwards, ${textShadow} 2000ms ease-in 2000ms forwards;
  pointer-events: none;
`

const ClippedG = styled(Clipped)`
  top: 100px;
  left: 2px;
`

